@import url("./__container/popup__container.css");
@import url("./__container/_type/popup__container_type_image.css");
@import url("./__container/_type/popup__container_type_confirmation.css");
@import url("./__title/popup__title.css");
@import url("./__close-button/popup__close-button.css");
@import url("./__image/popup__image.css");
@import url("./__image-label/popup__image-label.css");
@import url("./__status-icon/popup__status-icon.css");

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 50;
  visibility: hidden;
  background: rgba(0, 0, 0, 0.6);
  opacity: 0;
  transition: all 1s ease-in-out;
}
