@import url("./_type/form__input_type_error.css");
@import url("./_theme/form__input_theme_dark.css");

.form__input {
  width: 100%;
  height: 27px;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #000000;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.form__input.form__input_theme_dark {
  background-color: #000;
  border-bottom: 2px solid #cccccc;
  color: #cccccc;
}

.form__input.form__input_type_error {
  border-bottom: 1px solid #ff0000;
}
